<template>
  <div class="content-box">
    <p class="f30 mb-30">银行卡充值</p>
    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="120">
      <FormItem label="收款人">
        {{$route.query.bankUser}}
      </FormItem>
      <FormItem label="银行名称">
        {{$route.query.bankName}}
      </FormItem>
      <FormItem label="收款账号">
        {{$route.query.bankNum}}
      </FormItem>
      <FormItem label="充值金额(元)" prop="amount">
        <input @blur="inputBlur" class="ivu-input" v-model="formValidate.amount" style="width:300px"
          maxlength="12"></input>
      </FormItem>
      <FormItem label="汇款人手机号" prop="mch_phone_num">
        <Input style="width:300px" v-model="formValidate.mch_phone_num"></Input>
      </FormItem>
      <FormItem label="备注">
        <Input v-model="formValidate.remark" type="textarea" style="width:300px" :rows="4"></Input>
      </FormItem>
      <FormItem>
        <Button type="primary" style="width:120px" @click.native="handleClick" :loading="loading">提交</Button>
      </FormItem>
    </Form>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters(['userInfo'])
  },
  data () {
    const validatePhone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请填写手机号码'));
      } else if (/^1[3456789]\d{9}$/.test(value)) {
        callback();
      } else {
        callback(new Error('请正确填写手机号码'));
      }
    };
    const validateAmount = (rule, value, callback) => {
      let reg = /^([0-9]*|[0-9]*\.[0-9]{1,2})$/
      if (value > 20000000) {
        return callback('金额不能大于两千万')
      }
      if (value === '' || value == 0 || value == '0.00' || value == '0.0') {
        callback(new Error('请输入金额'));
      } else if (reg.test(value.trim())) {
        callback()
      } else {
        callback(new Error('请正确输入金额'));
      }
    }
    return {
      loading: false,
      formValidate: {
        amount: '',
        mch_phone_num: '',
        remark: ''
      },
      ruleValidate: {
        amount: [
          { required: true, validator: validateAmount, trigger: 'change' }
        ],
        mch_phone_num: [
          { required: true, validator: validatePhone, trigger: 'blur' }
        ]
      }
    }
  },
  watch: {
    'formValidate.amount' (val, oldVal) {
      let reg = /^(0?\.?[0-9]?|0\.[0-9][1-9]|[1-9][0-9]*|[0-9]*\.[0-9]?[0-9]?)$/
      let reg1 = /^0[1-9]$/

      if (reg1.test(val)) {
        return this.formValidate.amount = val.substr(1)
      }
      if (reg.test(val)) {
        this.formValidate.amount = val
      } else if (val != '') {
        this.formValidate.amount = oldVal
      }
    }
  },
  mounted () {
    this.formValidate.mch_phone_num = this.userInfo.phone_num
  },
  methods: {
    inputBlur () {
      this.$refs.formValidate.validateField('amount')
    },
    handleClick () {
      this.$refs.formValidate.validate((valid) => {
        if (valid) {
          let params = {
            econo_name: this.$route.query.bankName,
            econo_account: this.$route.query.bankNum,
            econo_owner: this.$route.query.bankUser,
            amount: parseInt(this.formValidate.amount * 100),
            remark: this.formValidate.remark,
            mch_phone_num: this.formValidate.mch_phone_num
          }
          this.loading = true
          this.$post('cgi/recharge', params).then(resData => {
            this.loading = false
            if (resData.ret_code == 0) {
              this.$Message.success('充值申请提交成功')
              this.$router.push({ path: '/charge', query: { name: "2" } })
            }
          }).catch(() => {
            this.loading = false
          })
        }
      })
    }
  }
}
</script>